import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import CampaignList from './camping/campinglist';
import CampaignCreationForm from './camping/camingcreate';
import TransactionHistory from './camping/TransactionHistory';
import dummyTransactions from './dummyData';

const Tab = () => {
  const [activeTab, setActiveTab] = useState(0);

  const handleTabClick = (index) => {
    setActiveTab(index);
  };

  const tabButtonStyle = {
    backgroundColor: 'grey',
    color: 'black',
  };

  return (
    <div>
      <ul className="nav nav-tabs">
        <li className="nav-item">
          <button
            className={`nav-link ${activeTab === 0 ? 'active' : ''}`}
            onClick={() => handleTabClick(0)}
            style={activeTab === 0 ? tabButtonStyle : {}}
          >
            Campaign List
          </button>
        </li>
        <li className="nav-item">
          <button
            className={`nav-link ${activeTab === 1 ? 'active' : ''}`}
            onClick={() => handleTabClick(1)}
            style={activeTab === 1 ? tabButtonStyle : {}}
          >
            Create Campaign
          </button>
        </li>
        <li className="nav-item">
          <button
            className={`nav-link ${activeTab === 2 ? 'active' : ''}`}
            onClick={() => handleTabClick(2)}
            style={activeTab === 2 ? tabButtonStyle : {}}
          >
            Transaction History
          </button>
        </li>
      </ul>

      <div className="tab-content">
        <div className={`tab-pane ${activeTab === 0 ? 'active' : ''}`}>
        {activeTab === 0 && <CampaignList />}

        </div>
        <div className={`tab-pane ${activeTab === 1 ? 'active' : ''}`}>
        {activeTab === 1 && <CampaignCreationForm />}

        </div>
        <div className={`tab-pane ${activeTab === 2 ? 'active' : ''}`}>
        {activeTab === 2 && <TransactionHistory dummyTransactions={dummyTransactions} />
}
        </div>
      </div>
    </div>
  );
};

export default Tab;
