import React, { useState ,useEffect} from 'react';
import { Modal, Form, Button, Alert } from 'react-bootstrap';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { FiEye, FiEyeOff } from 'react-icons/fi'; // Import FiEye and FiEyeOff icons
import './custom.css';
import { loginSuccess } from '../Redux/action';
import { useDispatch } from 'react-redux';

const Login = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [loginError, setLoginError] = useState(''); // Define loginError state
  const navigate = useNavigate();
  const [showModal, setShowModal] = useState(false);
  // Validation state
  const [emailError, setEmailError] = useState('');
  const [passwordError, setPasswordError] = useState('');
  const dispatch=useDispatch();
 
 

  const handlePasswordChange = (e) => {
    const value = e.target.value;
    setPassword(value);

    // Add password validation (you can customize this)
    if (!value || value.length < 6) {
      setPasswordError('Password must be at least 6 characters long.');
    } else {
      setPasswordError('');
    }
  };
  useEffect(() => {
    setShowModal(true);
  }, []);

  const closeModal = () => {
    setShowModal(false);
    setError(false);

    navigate("/");
  };
  const togglePasswordVisibility = () => {
    setShowPassword(!showPassword);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Check for validation errors
    if (emailError || passwordError) {
      return;
    }

     try {
      const response = await axios.post('https://donation-backend-six1.onrender.com/api/login', {
        username,
        password,
      });

      if (response.data) {
     dispatch(loginSuccess(response.data));
        navigate("/home");
      } else {
        setError('Login failed. Please check your credentials.');
      }
    } catch (error) {
      setError('Invalid email or password.');
    }

    // The return statement should be placed inside this function
  };
  
  // Move the return statement inside the component function
  return ( <>
     <Modal show={showModal} onHide={closeModal} centered>
    <Modal.Header closeButton>
            <Modal.Title className="title">Login Form</Modal.Title>
          </Modal.Header>
      <Modal.Body>
        {error && <Alert variant="danger">{error}</Alert>}
        <Form onSubmit={handleSubmit}>
          <Form.Group>
            <Form.Label>Username</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
            />
            {emailError && <small className="text-danger">{emailError}</small>}
          </Form.Group>
          <Form.Group>
            <Form.Label>Password</Form.Label>
            <div className="password-input">
              <Form.Control
                type={showPassword ? 'text' : 'password'}
                placeholder="Password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
              <span
                className="password-toggle-icon"
                onClick={togglePasswordVisibility}
              >
                {showPassword ? <FiEyeOff /> : <FiEye />}
              </span>
            </div>
            {passwordError && <small className="text-danger">{passwordError}</small>}
          </Form.Group>
          <Button variant="primary" type="submit">
            Login
          </Button>
          <p className="login-p">
  Don't have an account?{' '}
  <span
    className="login-span"
    onClick={() => {
      // Use the navigate function to direct to the signup page
      navigate('/signup');
    }}
    style={{ cursor: 'pointer' }}
  >
    Sign Up
  </span>
</p>
      </Form>

      {loginError && <Alert variant="danger">{loginError}</Alert>}

    
      </Modal.Body>
    </Modal>
    </>
  );
};

export default Login;