import React from 'react';
import { Container, Nav, Navbar, Button, Dropdown } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSignInAlt, faUserPlus, faUser } from '@fortawesome/free-solid-svg-icons';
import { Link, useNavigate } from 'react-router-dom';
import Google from '../usermanagement/Google';
function Header() {
  const navigate = useNavigate();

  const handleGoogleLogin = () => {
    navigate('/');
  };

  return (
    <Navbar bg="light" variant="light" className="bg-white"> {/* Add bg-white class for white background */}
      <Container>
        <div className="d-flex align-items-center">
          <img
            src="/logo.png" // Replace with your image path or URL
            alt="Logo"
            width="70" // Adjust the width as needed
            height="50" // Adjust the height as needed
            className="d-inline-block align-top m-3 "
          />
          <Navbar.Brand href="#home">SEVA</Navbar.Brand>
        </div>
      <Nav className="me-auto">
        </Nav>
        <div className="d-flex align-items-center">
          {/* <Button variant="primary" className="mr-2">
            Start a Fundraiser
          </Button> */}
          <Dropdown align="end">
            <Dropdown.Toggle variant="light" id="dropdown-basic">
              <span>
                <FontAwesomeIcon icon={faUser} size="lg" />
              </span>
            </Dropdown.Toggle>
            <Dropdown.Menu>
              <Dropdown.Item as={Link} to="/login">
                <FontAwesomeIcon icon={faSignInAlt} className="mr-2" />
                Login
              </Dropdown.Item>
              <Dropdown.Item as={Link} to="/signup">
                <FontAwesomeIcon icon={faUserPlus} className="mr-2" />
                Signup
              </Dropdown.Item>
              <Dropdown.Item onClick={handleGoogleLogin}>
                <Google />
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </div>
      </Container>
    </Navbar>
  );
}

export default Header;
