import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faGraduationCap,
  faMedkit,
  faMonument,
  faQuestionCircle,
} from '@fortawesome/free-solid-svg-icons';


function CampaignCreationForm() {
  const [campaignData, setCampaignData] = useState({
    campaignName: '',
    campaignGoal: '',
    campaignDescription: '',
    campaignCategory: 'Education',
    campaignImage: null,
  });

  const apiUrl = 'https://donation-backend-six1.onrender.com';

  const handleChange = (e) => {
    const { name, value } = e.target;
    setCampaignData({ ...campaignData, [name]: value });
  };

  const handleImageChange = (e) => {
    const selectedImage = e.target.files[0];
    setCampaignData({ ...campaignData, campaignImage: selectedImage });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const formData = new FormData();
    formData.append('campaignName', campaignData.campaignName);
    formData.append('campaignGoal', campaignData.campaignGoal);
    formData.append('campaignDescription', campaignData.campaignDescription);
    formData.append('campaignCategory', campaignData.campaignCategory);
    formData.append('campaignImage', campaignData.campaignImage);

    try {
      const response = await fetch(`${apiUrl}/create-campaign`, {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        throw new Error(`Failed to create campaign (${response.status} ${response.statusText})`);
      }

      const data = await response.json();
      console.log(data.message);

      // Clear form data after submission
      setCampaignData({
        campaignName: '',
        campaignGoal: '',
        campaignDescription: '',
        campaignCategory: 'Education',
        campaignImage: null,
      });
    } catch (error) {
      console.error('Error:', error);
    }
  };

  const campaignCategories = [
    {
      label: 'Education',
      icon: faGraduationCap,
    },
    {
      label: 'Medical',
      icon: faMedkit,
    },
    {
      label: 'Memorial',
      icon: faMonument,
    },
    {
      label: 'Other',
      icon: faQuestionCircle,
    },
  ];

  return (
    <div className="container mt-5">
    <div className="row justify-content-center">
      <div className="col-md-6">
        <h5 className="mb-3">Create a Fundraising Campaign</h5>
        <form onSubmit={handleSubmit} id="campaignForm">
        <div className="form-group">
          <label htmlFor="campaignName">Campaign Name</label>
          <input
            type="text"
            className="form-control"
            id="campaignName"
            name="campaignName"
            value={campaignData.campaignName}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="campaignGoal">Campaign Goal</label>
          <input
            type="number"
            className="form-control"
            id="campaignGoal"
            name="campaignGoal"
            value={campaignData.campaignGoal}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="campaignDescription">Campaign Description</label>
          <textarea
            className="form-control"
            id="campaignDescription"
            name="campaignDescription"
            value={campaignData.campaignDescription}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label>Campaign Category</label>
          <div className="row">
            {campaignCategories.map((category, index) => (
              <div className="col-md-3" key={index}>
                <div className="form-check">
                  <input
                    type="radio"
                    className="form-check-input"
                    id={`category${index}`} // Corrected line
                    name="campaignCategory"
                    value={category.label}
                    checked={campaignData.campaignCategory === category.label}
                    onChange={handleChange}
                  />
                  <label
                    className="form-check-label"
                    htmlFor={`category${index}`} // Corrected line
                    style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}
                  >
                    <FontAwesomeIcon icon={category.icon} size="2x" />
                    {category.label}
                  </label>
                </div>
              </div>
            ))}
          </div>
        </div>
        <div className="form-group">
          <label htmlFor="campaignImage">Campaign Image</label>
          <input
            type="file"
            className="form-control-file"
            id="campaignImage"
            name="campaignImage"
            accept="image/*"
            onChange={handleImageChange}
          />
        </div>
        <button type="submit" className="btn btn-primary">
          Create Campaign
        </button>
      </form>
    </div>
    </div>
    </div>
  );
}

export default CampaignCreationForm;
