import React, { useState, useEffect } from "react";
import {
  Modal,
  Button,
  Form,
  Alert,
  Container,
  Row,
  Col,
} from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
// Include your custom CSS file for background color and additional styling
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { FiEye, FiEyeOff } from "react-icons/fi"; // Import the FiEye and FiEyeOff icons
import { setMongoDBData } from "../Redux/action";
import { useDispatch } from "react-redux";

const Signup = (props) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const dispatch = useDispatch();

  const navigate = useNavigate();

  const [submitted, setSubmitted] = useState(false);
  const [error, setError] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [showPassword, setShowPassword] = useState(false);

  const [username, setUsername] = useState(""); // Define username state
  const [loginError, setLoginError] = useState(""); // Define loginError state

  const handleValidation = () => {
    if (!username || !password) {
      setLoginError("Username and password are required.");
      return false;
    }
    // Add more validation logic here if needed
    return true;
  };

  const togglePasswordVisibility = () => {
    setShowPassword(!showPassword);
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const emptyFields = {};

    if (username.trim() === "") {
      emptyFields.fullName = true;
    }
    if (email.trim() === "") {
      emptyFields.email = true;
    }
    if (password.trim() === "") {
      emptyFields.password = true;
    }

    if (Object.keys(emptyFields).length > 0) {
      setError("Please fill in all required fields");
      setSubmitted(true);
    } else if (!isValidEmail(email)) {
      setError("Invalid email format");
      setSubmitted(true);
    } else {
      // Make the Axios request here
      if (!handleValidation()) {
        return;
      }

      const data = {
        username,
        email,
        password,
      };

      axios
        .post("https://donation-backend-six1.onrender.com/users/register", data)
        .then((response) => {
          if (response.data) {
            dispatch(setMongoDBData(response.data));
            navigate("/home");
          } else {
            setError("Failed to register"); // Handle registration failure
          }
        })
        .catch(() => {
          setError("An error occurred"); // Handle Axios request error
        });
    }
  };

  const isValidEmail = (value) => {
    // Basic email format validation
    const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailPattern.test(value);
  };

  useEffect(() => {
    setShowModal(true);
  }, []);

  const closeModal = () => {
    setShowModal(false);
    setError(false);

    navigate("/");
  };

  return (
    <>
      <Modal show={showModal} onHide={closeModal} centered>
        <Modal.Header closeButton>
          <Modal.Title className="title">User Registration</Modal.Title>
        </Modal.Header>
        <Modal.Body className="custom-modal-body">
          <Container style={{ maxWidth: "800px" }}>
            {/* Adjust the maxWidth value as needed */}
            <Row className="align-items-center">
              <Col lg={6} className="text-center">
                {/* Your app name and logo */}
                <div>
                  <img
                    src="/logo88.png"
                    alt="Logo"
                    className="app-logo"
                    style={{ maxWidth: "100%" }}
                  />
                </div>
              </Col>
              <Col xl={6}>
                {/* Registration form */}
                <Form
                  style={{ backgroundColor: "#f2f2f2", padding: "20px" }}
                  onSubmit={handleSubmit}
                >
                  <Form.Group>
                    <Form.Label>Username</Form.Label>
                    <Form.Control
                      value={username}
                      type="text"
                      placeholder="Enter Username"
                      className={`form-control ${
                        submitted && !username.trim() ? "is-invalid" : ""
                      }`}
                      onChange={(e) => setUsername(e.target.value)}
                    />
                  </Form.Group>

                  <Form.Group>
                    <Form.Label>Email</Form.Label>
                    <Form.Control
                      value={email}
                      type="email"
                      placeholder="Enter Email"
                      className={`form-control ${
                        submitted && !email.trim() ? "is-invalid" : ""
                      }`}
                      onChange={(e) => setEmail(e.target.value)}
                    />
                  </Form.Group>

                  <Form.Group>
                    <Form.Label>Password</Form.Label>
                    <div className="password-input">
                      <Form.Control
                        value={password}
                        type={showPassword ? "text" : "password"} // Toggle password visibility
                        placeholder="Enter Password"
                        className={`form-control ${
                          submitted && !password.trim() ? "is-invalid" : ""
                        }`}
                        onChange={(e) => setPassword(e.target.value)}
                      />
                      <span
                        className="password-toggle-icon"
                        onClick={togglePasswordVisibility}
                      >
                        {showPassword ? <FiEyeOff /> : <FiEye />}
                      </span>
                    </div>
                  </Form.Group>

                  <br />
                  <Button variant="primary" type="submit" className="btn-block">
                    Submit
                  </Button>
                  <p className="login-p">
                    Don't have an account?{" "}
                    <span
                      className="login-span"
                      onClick={() => {
                        // Use the navigate function to direct to the signup page
                        navigate("/login");
                      }}
                      style={{ cursor: "pointer" }}
                    >
                      Login
                    </span>
                  </p>
                </Form>
                {loginError && <Alert variant="danger">{loginError}</Alert>}
              </Col>
            </Row>
          </Container>
        </Modal.Body>
      </Modal>
    </>
  );
};

export default Signup;
